    openTab = (src) => {
        let elem = this;
        var img = new Image();
        img.onload = function(){
            //alert( this.width+' '+ this.height );
            let docW = $( document ).width();
            let docH = $( document ).height();
            let adjustedW = 100*(parseFloat(this.width/docW)) + 10;
            let adjustedH = 100*(parseFloat(this.height/docH))+15;
            if(adjustedW > 80){
                let mod = parseFloat(80/adjustedW);
                adjustedW *= mod;
                adjustedH *= mod;
            }
            if(adjustedH > 80){
                let mod = parseFloat(80/adjustedH);
                adjustedW *= mod;
                adjustedH *= mod;
            }
            elem.props.openTab("magnify",src, adjustedH, adjustedW);
        };
        img.src = src;
        
    }
