function Time(props) {
    //<p>{props.time.toLocaleTimeString()}</p>
  return (
    <p>{moment(props.time).format('MMMM Do YYYY, h:mm:ss a')}</p>
  );
}

class Clock extends React.Component {
  render() {
      return (
        <div className="clock">
          <Time time={this.props.time} />
        </div>
      );
   }
}

function tick() {
  ReactDOM.render(
    <Clock title="ReactJS Clock" time={new Date()} />,
    document.getElementById('clock')
  );
}

